.chat-container {
    width: 900px;
    height: 530px;
    border-radius: 7px;
    -webkit-box-shadow: 0px 0px 19px -2px rgba(122,113,122,1);
    box-shadow: 0px 0px 19px -2px rgba(122,113,122,1);
    margin : 90px auto 0 auto;
}
.left {
    width: 350px;
    float: left;
    background-color: #FBFCFB;
    border-right: 1px solid #EBEEED;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
}
.right {
    width: 500px;
    float: left;
}
.request-icon {
    position: absolute;
    color: #296ddb;
    top: -20px;
    left: 18px;
    font-size: 16px;
    display: none;
}
.new-message-icon {
    position: absolute;
    color: #296ddb;
    top: -20px;
    left: 66px;
    font-size: 16px;
}

.me-container {
    height: 30px;
    width: 900px;
    border-radius: 7px 7px 0 0;
    background-color: #EDF0EF;
}
.me-right {
    width: 110px;
    height: 30px;
    background-color: rgba(212,212,217,0.42);
    float: right;
    position: relative;
}
.me-img {
    width: 20px;
    margin: 5px 5px 0 0;
    float: right;
}
.me-name {
    float: right;
    font-size: 12px;
    margin: 5px 5px 0 0;
    color: #6a6a6c;
}
.me-link, .me-link-span {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}
.me-signout {
    float: left;
    margin-left: 7px;
    margin-top: 3px;
    color: rgba(196,85,64,0.89);
    text-decoration: none;
}
.me-signout:hover {
    text-decoration: none;
    color: rgba(206,89,68,0.53);
}

.users-container {
    width: 350px;
    display: inline-block;
    float: left;
}
.users-wrap {
    width: 350px;
    height: 390px;
    overflow: hidden;
    overflow-y: scroll;
}
.users-search-wrap {
    height: 50px;
    width: 100%;
}
.search-form {
    max-width: 100%;
    min-width: 100%;
    height: 100%;
    resize: none;
    color: #6e6e6e;
    opacity: 0.8;
    font-size: 14px;
    border: none;
    padding: 12px 8px 8px 30px;
}
.search-form:focus {
    outline: none;
}

.active {
    background-color: #D3ECF2!important;
}
.user-container {
    background-color: #f4f7fc;
    border-bottom: 1px solid #E9EFEF;
    height: 80px;
    padding: 15px 15px 0 10px;
    position: relative;
}
.user-container:hover {
    background-color: #ebeef4;
}
.user-img {
    width: 50px;
    float: left;
    margin-right: 20px;
}
.user-name {
    font-weight: 14px;
    margin-bottom: 0;
}
.user-link, .user-link-span {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    line-height: 100px;
    display: block;
}

.send-container {
    display: inline-block;
    float: left;
    width: 549px;
}
.send-form {
    max-width: 100%;
    min-width: 100%;
    height: 50px;
    resize: none;
    color: #6e6e6e;
    opacity: 0.8;
    font-size: 14px;
    border: none;
    padding: 12px 8px 8px 30px;
}
.send-form:focus {
    outline: none;
}

.user-info-container {
    height: 60px;
    width: 549px;
    float: left;
    /*background-color: rgba(227,227,232,0.21);*/
    background-color: #FAFAFA;
    border-bottom: 1px solid rgba(182,182,186,0.21);
    position: relative;
}
.user-info-img {
    width: 40px;
    margin: 4px 15px 5px 10px;
    float: left;
}
.user-info-name {
    margin: 6px 0 0 55px;
    display: block;
    padding: 0;
}
.friend-btn {
    background-color: #65c162;
    color: white!important;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    font-size: 13px;
    padding: 2px 10px;
    font-weight: bold;
}
.friend-btn:focus {
    outline: none;
}
.friend-btn:hover {
    background-color: #5ca359;
}
.request-sent {
    font-size: 13px;
    color: #979ca3;
}
.request-sent > .fa-check {
    color: #65c162;
    margin-left: 10px;
}

.profile-container {
    height: 440px;
    width: 350px;
    display: inline-block;
    float: left;
    border-top: 1px solid #EFF2F2;
}
.profile-img {
    width: 70px;
    margin: 20px 20px 0 10px;
    float: left;
}
.profile-name {
    display: block;
    margin-top: 30px;
}
.profile-email {
    float: left;
    font-size: 13px;
    color: #b6bdc6;
}
.profile-requests {
    margin-top: 55px;
    padding-top: 10px;
    border-top: 1px solid #EFF2F2;
    overflow-y: scroll;
    height: 300px;
}
ul, li {
    list-style: none;
}
.profile-requests > ul > li {
    margin-bottom: 5px;
}
.profile-request-img {
    width: 25px;
    margin: 5px;
}
.profile-request-name {
    margin-right: 10px;
}

