#messages-container {
    width: 550px;
    height: 390px;
    float: left;
    position: relative;
    display: inline-block;
}
#messages-container::after {
    content: "";
    background-image: url(/static/img/cover.c5b0cb8.jpg);
    opacity: 0.11;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;
}
.messages-wrap {
    height: 100%;
    overflow-y: scroll;
}

.message-container {
    padding: 10px;
}
.message-wrap {
    float: left;
    margin: 10px 0 10px 0;
}
.message-wrap > .message-img {
    width: 50px;
    float: left;
}
.message-wrap > .message-body-wrap {
    padding: 4px 8px;
    border-radius: 16px;
    background-color: white;
    margin-left: 60px;
    width: 330px;
    -webkit-box-shadow: 1px 1px 2px 0px rgba(194,177,194,1);
            box-shadow: 1px 1px 2px 0px rgba(194,177,194,1);
}
.message-body {
    margin: 5px;
    font-size: 14px;
    color: #525253;
}
.message-user {
    color: #b1b1b5;
    margin: 0;
    font-size: 12px;
}
.message-date {
    float: right;
    font-size: 12px;
}
.right > .message-img {
    float: right;
}
.right > .message-body-wrap {
    float: right;
    margin-right: 10px;
}
.right > .message-body-wrap {
    background-color: #EDFFC5;
}

.conversations-container {
    width: 350px;
    display: inline-block;
    float: left;
}
.conversations-wrap {
    width: 350px;
    height: 440px;
    overflow: hidden;
    overflow-y: scroll;
}

.active {
    background-color: #D3ECF2!important;
}
.conversation-container {
    background-color: #f4f7fc;
    border-bottom: 1px solid #E9EFEF;
    height: 80px;
    padding: 15px 15px 0 10px;
    position: relative;
}
.conversation-container:hover {
    background-color: #DBF0F5;
}
.conversation-img {
    width: 50px;
    float: left;
    margin-right: 20px;
}
.conversation-user {
    font-size: 16px;
    margin-bottom: 0;
    color: #525254;
}
.conversation-message {
    width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 14px;
    color: #7c7c7e;
}
.conversation-link, .conversation-link-span {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}
.conversation-date {
    font-size: 12px;
    float: right;
    color: #818184;
}
.conversation-unseen-messages {
    position: absolute;
    right: 20px;
    bottom: 0;
    background-color: #72a6f9;
    border-radius: 5px;
    font-size: 14px;
    padding: 0 8px;
    color: white;
}
